import React, { useState, useEffect } from 'react'
import { Label, Input, FormGroup, Button } from 'reactstrap'
import { X } from 'react-feather'
import PerfectScrollbar from 'react-perfect-scrollbar'
import { Spinner } from 'reactstrap'
import classnames from 'classnames'
import themeConfig from '../../configs/themeConfig'

const DataListSidebar = (props) => {
  const [relation, setrelation] = useState('Cigretes')
  const [relation1, setrelation1] = useState('other')
  const [date, setdate] = useState('')
  const [address, setaddress] = useState('')
  const [contact1, setcontact1] = useState('')
  const [id, setid] = useState('')
  const [name, setname] = useState('')
  const [price, setprice] = useState('')
  const [addNew, setaddNew] = useState(false)

  const nullData = () => {
    setid('')
    setname('')
    setrelation('')
    setrelation1('')
    setdate('')
    setaddress('')
    setcontact1('')
    setprice('')
  }
  useEffect(() => {
    if (props.data) {
      const { id, name, relation, address, date, primaryContact, price } =
        props.data
      setid(id)
      setname(name)
      setrelation(relation)
      if (
        relation !== 'Cigretes' &&
        relation !== 'Drink' &&
        relation !== 'Fees' &&
        relation !== 'Fuel' &&
        relation !== 'Groceries' &&
        relation !== 'Party' &&
        relation !== 'Rent' &&
        relation !== 'Snacks' &&
        relation !== 'Travel'
      ) {
        setrelation('others')
        setrelation1(relation)
      }
      setaddress(address)
      setdate(date)
      setcontact1(primaryContact)
      setprice(price)
    }
    if (!props.data || addNew) {
      nullData()
    }
    setaddNew(false)
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [props.data])

  const handleSubmit = () => {
    const obj = {
      relation: relation?.trim(),
      relation1: relation1?.trim(),
      date: date?.trim(),
      address: address?.trim(),
      contact1: contact1?.trim(),
      _id: id?.trim(),
      name: name?.trim(),
      price: price?.trim()
    }
    if (props.data !== null) {
      obj._id = props.data._id
      obj.usage = props.data.usage
      props.updateData(obj)
    } else {
      setaddNew(true)
      props.addData(obj)
    }
  }

  const { show, handleSidebar, data } = props
  return (
    <div
      className={classnames('data-list-sidebar', {
        show: show
      })}
    >
      <div className="data-list-sidebar-header mt-2 px-2 d-flex justify-content-between">
        <h4>{data !== null ? 'UPDATE SPEND DETAILS' : 'ADD NEW SPEND'}</h4>
        <X
          size={20}
          onClick={() => {
            handleSidebar(false, true)
            nullData()
          }}
        />
      </div>
      <PerfectScrollbar
        className="data-list-fields px-2 pt-2"
        options={{ wheelPropagation: false }}
      >
        <FormGroup className="form-label-group">
          <Input
            className="input-label"
            type="text"
            value={name}
            placeholder="Item"
            onChange={(e) => setname(e.target.value)}
            id="data-name"
          />
          <Label
            className={
              themeConfig.theme === 'dark' ? 'dark-label' : 'light-label'
            }
            for="data-name"
          >
            Item
          </Label>
        </FormGroup>
        <FormGroup>
          <Input
            type="select"
            id="data-category"
            value={relation}
            onChange={(e) => setrelation(e.target.value)}
          >
            <option>Cigretes</option>
            <option>Drink</option>
            <option>Fees</option>
            <option>Fuel</option>
            <option>Groceries</option>
            <option>Party</option>
            <option>Rent</option>
            <option>Snacks</option>
            <option>Travel</option>
            <option>others</option>
          </Input>
          <Label
            className={
              themeConfig.theme === 'dark'
                ? 'dark-label select-label'
                : 'light-label select-label'
            }
            for="data-category"
          >
            Label *
          </Label>
        </FormGroup>
        {relation === 'others' && (
          <FormGroup>
            <Input
              className="input-label"
              type="text"
              value={relation1}
              placeholder="Label Name"
              onChange={(e) => relation1(e.target.value)}
              id="data-name"
            />
          </FormGroup>
        )}
        <FormGroup className="form-label-group">
          <Input
            className="input-label"
            type="date"
            value={date}
            placeholder="Enter Date"
            onChange={(e) => setdate(e.target.value)}
            id="data-name"
          />
          <Label
            className={
              themeConfig.theme === 'dark' ? 'dark-label' : 'light-label'
            }
            for="data-name"
          >
            Date *
          </Label>
        </FormGroup>
        <FormGroup className="form-label-group">
          <Input
            className="input-label"
            type="number"
            value={contact1}
            onChange={(e) => setcontact1(e.target.value)}
            id="data-price"
            placeholder="Enter spend amount"
          />
          <Label
            for="data-price"
            className={
              themeConfig.theme === 'dark' ? 'dark-label' : 'light-label'
            }
          >
            Amount *
          </Label>
        </FormGroup>

        <FormGroup className="form-label-group">
          <Input
            className="input-label "
            style={{ height: '115px' }}
            type="textarea"
            value={address}
            placeholder="Description"
            onChange={(e) => setaddress(e.target.value)}
            id="data-name"
          />
          <Label
            className={
              themeConfig.theme === 'dark' ? 'dark-label' : 'light-label'
            }
            for="data-name"
          >
            Description *
          </Label>
        </FormGroup>
      </PerfectScrollbar>
      <div className="data-list-sidebar-footer px-2 d-flex justify-content-start align-items-center mt-1">
        <Button.Ripple
          // className="ml-1"
          className="button-label"
          color="secondary"
          outline
          onClick={() => {
            handleSidebar(false, true)
            nullData()
          }}
        >
          Cancel
        </Button.Ripple>
        <Button.Ripple
          color="warning"
          type="reset"
          className="button-label"
          onClick={() => handleSubmit()}
        >
          {props.isLoading && <Spinner color="danger" size="sm" />}{' '}
          {data !== null
            ? props.isLoading
              ? 'Updating...'
              : 'Update'
            : props.isLoading
            ? 'Adding...'
            : 'Add'}
        </Button.Ripple>
      </div>
    </div>
  )
}
export default DataListSidebar
